a,
a:focus,
a:hover {
  color: #fff;
  text-decoration: underline;
}

html,
body {
  background-color: #333;
}

#fb-warn {
  display: none;
}

body {
  display: flex;

  width: 100%;
  min-height: 100vh;

  box-shadow: inset 0 0 5rem rgba(0, 0, 0, .5);
}

.container {
  max-width: 42em;
}

#title-logo-container {
  width: 6em;
  max-width: 20vw;
  box-shadow: 0 0 21px 0 rgba(255,255,255,0.75);
}

#title-logo {
  width: 100%;
  z-index: -1;
}

#start-box .search-input:disabled {
  background-color: #aaa;
}

#start-fieldset:disabled .non-selected-wrapper {
  background-color: #888;
  overflow: hidden;
}

#start-fieldset:disabled .selected-wrapper {
  background-color: #bbb;
  overflow: hidden;
}

fieldset:disabled .bg-light {
  background-color: #888888!important;
}

.show-on-disabled {
  display: none;
}

fieldset:disabled .show-on-disabled {
  display: block;
  color: white;
  background-color: black;
  padding: 4px;
  margin: 4px 0 0 0;
  border-radius: 8px;
  text-align: center;
  font-weight: bold;
}

/* Hide the boxes further down the page until they're needed. */
#loading-box,
#sign-in-box,
#activity-select-box,
#activity-edit-box,
#final-box-google,
#final-box-non-google {
  display: none;
}

#btn-authorize img {
  height: 1.5em;
  width: 1.5em;
}

.activity-select-dropdown {
  background-color: #007bff;
  cursor: pointer;
}
.activity-select-dropdown:hover {
  background-color: #0055b4;
}
.activity-select-dropdown:active {
  background-color: #003776;
}
#activity-select-fieldset:disabled .activity-select-dropdown {
  background-color: #0a163b !important;
}

.activity-select-toggle {
  font-weight: bolder;
  width: 7.5em;
  cursor: pointer;
}

.act-selected {
  background-color: #00ff3f;
}
.act-selected:hover {
  background-color: #009724;
}
.act-selected:active {
  background-color: #00721f;
}
#activity-select-fieldset:disabled .act-selected {
  background-color: #00320f !important;
}

.act-not-selected {
  background-color: #f82c3f;
}
.act-not-selected:hover{
  background-color: #a02735;
}
.act-not-selected:active{
  background-color: #840f1b;
}
#activity-select-fieldset:disabled .act-not-selected {
  background-color: #50070f !important;
}

.activity-edit-week {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 1.6em;
  height: 1.6em;
  border: 1px solid black;
  border-radius: 4px;
  margin-right: 3px;
  margin-bottom: 3px;
}
.activity-edit-week.activity-active-week {
  background-color: black;
  color: white;
}

.activity-color-pick {
  width: 2em;
  height: 2em;
  cursor: pointer;
}

.btn-fb-share {
  text-decoration: none;
}

.btn-fb-share img {
  height: 1.5em;
  width: 1.5em;
  margin-bottom: 2px;
  margin-right: 4px;
}
